.comments_container .comment .comment-item:hover .circle-avatar {
  animation: rotate_circle 4000ms linear infinite both;
}
.comments_container .comment.depth-2 {
  margin-left: 170px;
  position: relative;
}
.comments_container .comment.depth-2::before {
  content: "";
  width: 100px;
  height: 100px;
  background: url("../svg/comment.svg") center center no-repeat;
  position: absolute;
  left: -150px;
  top: 50%;
  margin-top: -50px;
}
.comments_container .left_part {
  width: 120px;
  margin-right: 25px;
}
.comments_container .left_part .comment_date {
  color: #304566;
  text-align: center;
  margin-top: 20px;
  font-weight: 900;
  font-size: 16px;
  line-height: 22px;
  display: block;
}
.comments_container .commenter-avatar {
  width: 100px;
  position: relative;
  margin-left: 10px;
}
.comments_container .commenter-avatar .circle-avatar {
  position: absolute;
  top: -10px;
  left: -10px;
  width: calc(100% + 14px);
  height: calc(100% + 14px);
  border-radius: 100%;
  border: 3px dashed #ffd3bd;
}
.comments_container .commenter-avatar img {
  width: 100%;
  height: auto;
  border-radius: 100%;
}
.comments_container .comment-box {
  width: calc(100% - 145px);
}
.comments_container .comment-box a {
  color: inherit;
  transition: all 0.3s ease-in-out;
}
.comments_container .comment-box a:hover {
  color: #b3d0fd;
}
.comments_container .comment-box .info-meta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.comments_container .comment-box .info-meta .author {
  color: #ffa1ac;
  font-weight: 900;
  font-size: 20px;
  line-height: 28px;
}
.comments_container .comment-box .info-meta .reply-link {
  font-weight: 700;
  font-size: 16px;
  line-height: 28px;
  color: #ffa1ac;
}
.comments .comment-respond {
  background-color: #fefbf4;
  width: 100%;
  padding: 40px;
  margin-top: 20px;
}
.comments .comment-respond a {
  color: inherit;
  transition: all 0.3s ease-in-out;
}
.comments .comment-respond a:hover {
  color: #ffa1ac;
}
.comments h3.comment-reply-title a {
  font-size: 16px;
  color: #b3d0fd;
  transition: all 0.3s ease-in-out;
  padding-left: 10px;
}
.comments h3.comment-reply-title a:hover {
  color: #ffa1ac;
}
.comments h3.comments_title, .comments h3.commentreply-title {
  font-weight: 900;
  font-size: 40px;
  line-height: 40px;
  position: relative;
  z-index: 0;
  display: inline-block;
  margin-bottom: 15px;
}
.comments h3.comments_title::before, .comments h3.commentreply-title::before {
  content: "";
  width: calc(100% + 20px);
  height: 20px;
  border-radius: 20px;
  display: block;
  position: absolute;
  top: 100%;
  left: -10px;
  margin-top: -18px;
  background-color: #fff8b8;
  z-index: -1;
}
.comments .comment-form-comment,
.comments .comment-form-author,
.comments .comment-form-email,
.comments .comment-form-url {
  display: flex;
}